import React from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header } from 'components';
import { Layout, Container } from 'layouts';

import logo from '../../static/logo/anju.jpg';
const About = center => (
  <Layout>
    <Helmet title={'About Page'} />
    <Header title="About Page">I love web development</Header>
    <Container center={center}>
      <p>
        Hey there! I'm Anju Batta. I believe you are here because you want to know something about me, huh? I'm not good at explaining me, but yea, I've tried my best. I'm a learner. Years down the lane, I aspire to architect solutions. I don't have the patience for positioning a "div" on the web-page, if you ignore that, with all the critical thinking and multi-tasking I boast of, you can count me for a full-stack developer. I'm obsessed with coding, but I believe in thinking and planning before writing the code. I go mad when code isn't aligned. I like keeping the code DRY. The amount of code I punch in is directly proportional to the coffee I consume. And a little truth: I cannot survive without google and stackoverflow. .
      </p>
	  <div class="image-block">
	 <img src={logo} alt="Gatsby Logo" />
	  </div>
	  <div class="details">
	   Welcome to my website. I was born and raised in Chandigarh, India. I have completed my MCA in 2016 from LPU. I have dedicated my life to helping others. Iâ€™m a web developer with over 5 years in the IT industry, 250 websites and apps built to date. I'm Full-stack developers. The Full-stack developers are comfortable working with both the front and back ends. In The Odin Project, we focus on teaching you full-stack development, covering all aspects of web development.
	 </div>
    </Container>
  </Layout>
);

export default About;

Container.propTypes = {
  center: PropTypes.object,
};
